.App{
}


.header{
  position: fixed;
  top : 0;
  left : 0;
  height : 60px;
  width : 100vw;
  z-index: 2;
  background-color : #bb0000;
  color : #fff;
  -webkit-box-shadow: 0 5px 2px 0 rgba(0,0,0,0.12);
          box-shadow: 0 5px 2px 0 rgba(0,0,0,0.12);

  display : -ms-flexbox;

  display : flex;
  -ms-flex-flow : column nowrap;
      flex-flow : column nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack : center;
      justify-content : center;
  font-weight: bold;
  font-size : 1.5em;
}

.header > button {
  -ms-flex-item-align: end;
      align-self: flex-end;
}
.search{
  text-align: center;
}
.search input[type='text']{
  width : 200px;
  font-size : 1.2em;
}
.content{
  padding-top : 68px;
  display : -ms-flexbox;
  display : flex;
  -ms-flex-direction : column;
      flex-direction : column;
}
.loading{
  width : 100%;
  height : 70vh;
  display : -ms-flexbox;
  display : flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.results{
  padding : 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow : row wrap;
      flex-flow : row wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.videocard{
  padding : 5px;
  margin : 20px;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction : column;
      flex-direction : column;
  -ms-flex-align : center;
      align-items : center;
}
.videocard > .title{
  margin-bottom : 2px;
  margin-top : 2px;
}
.videocard > img,.videocard > .title{
  width : 300px;
}
.videocard > button{
  padding:2px;
  width : 80px;
  font-size : 1em;
  border : none;
  border-radius : 10px;
  background-color : #bb0000;
  color : white;
}
.controls{
  height : 30px;
  padding-bottom : 5px;
}
.controls > button{
  margin-left: 10px;
  margin-right: 10px;
  height : 25px;
  width : 25px;
}


